import React from "react";
import Chatbot from "react-chatbot-kit";
import "react-chatbot-kit/build/main.css";
import config from "../dependencies/config";
import MessageParser from "../dependencies/MessageParser";
import ActionProvider from "../dependencies/ActionProvider";
import ChatIcon from "@mui/icons-material/Chat";
import ClearIcon from "@mui/icons-material/Clear";
import "../styles/styles-chatbot.css";

export default function ChatBotBox() {
  const [showBot, toggleBot] = React.useState(false);

  function validate(text) {
    if (text.trim() === "") {
      return false;
    }
    return true;
  }

  return (
    <>
      <div className={showBot ? "chatbot-container show" : "chatbot-container"}>
        {/* {showBot && (
          <Chatbot
            className="chatbot-render"
            config={config}
            messageParser={MessageParser}
            actionProvider={ActionProvider}
            // saveMessages={saveMessages}
            // loadMessages={loadMessages()}
            validator={validate}
          />
        )} */}
        <Chatbot
          config={config}
          messageParser={MessageParser}
          actionProvider={ActionProvider}
          // saveMessages={saveMessages}
          // loadMessages={loadMessages()}
          validator={validate}
        />
      </div>
      <button
        className="chatbot-button"
        onClick={() => toggleBot((prev) => !prev)}
      >
        {showBot ? (
          <ClearIcon fontSize="large" />
        ) : (
          <ChatIcon fontSize="large" />
        )}
      </button>
    </>
  );
}
